form {
    display: flex;
    flex-direction: column;
    margin-top: 5%;
}

label {
    display: flex;
    justify-content: space-between;
    text-align: justify;
}

input {
    width: 70vw;
    border: none;
    border-bottom: 1px solid black;
}

form button {
    width: 50vw;
    align-self: center;
}